@import '../../../base';

.product-slider-new {
  max-width: 50%;
  position: fixed;
  top: 89px;
  z-index: 1031;
  padding: 0 15px !important;

  .input-wrapper-main-new {
    min-height: 0;
  }

  @media screen and (max-width: 991px) {
    max-width: 100%;
    position: relative;
    top: 25px;

    .input-wrapper-main-new {
      min-height: 48px;
    }

    .desc-bottom {
      transform: translateY(30px) !important;
    }
  }
}

.step-progress-description-container-new {
  transform: translateY(80px);
  max-width: 1108px;
  padding: 0 15px;
  margin-left: auto;
  margin-right: auto;

  @media screen and (max-width: 991px) {
    transform: translateY(20px);
  }

  .main {
    min-height: 36px;
    text-align: left;
    font-size: $font-size-smallest;
    font-weight: $font-weight-bold;
    color: $color-input-text;
    margin: 0;
    margin-left: 200px;

    @media screen and (max-width: 576px) {
      min-height: auto;
    }
    @media screen and (max-width: 991px) {
      margin-left: 0;
      text-align: center;
    }

    @media screen and (min-width: 992px) {
      display: inline-block;
      text-align: center;
      min-width: 230px;
    }
  }

  .sub {
    font-size: $font-size-regular;
    font-weight: $font-weight-normal;
    text-align: center;
    color: $color-body;
  }

  .back-button {
    position: absolute;
    left: 15px;
    font-size: $font-size-smallest;
    font-weight: $font-weight-bold;
    color: $color-input-text;

    @media screen and (max-width: 576px) {
      position: relative;
    }

    &:hover {
      text-decoration: none;
      color: $color-hover;
    }
  }
}
