@import '../../../base';

.slider-summary-container-new {
  @media screen and (min-width: 576px) {
    position: absolute;
    top: 0;
    right: 8px;
  }

  .prefix-suffix {
    font-size: $font-size-regular;
    font-weight: $font-weight-bold;
    color: $color-body;
  }

  .value {
    font-size: $font-size-regular;
    font-weight: $font-weight-heavy;
    color: $color-title;
  }
}

.slider-container-new {
  margin-bottom: 10px;
  position: relative;
  margin-left: auto;
  margin-right: auto;

  .line {
    width: 100%;
    height: 1px;
    border: solid 1px $color-line;
  }
  .line-green {
    position: absolute;
    top: 0;
    height: 1px;
    border: solid 1px $color-hover;
  }
  .color-knob {
    background-color: $color-hover;
  }
  .white-knob {
    background-color: #ffffff;
  }
  .slider-knob {
    position: absolute;
    left: 0;
    width: 21px;
    height: 21px;
    transform: translateX(-0.5px) translateY(-0.5px);
    
    box-shadow: 0 2px 8px 0 rgba(70, 115, 122, 0.2);
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 3;

    &:focus,
    &:active {
      outline: none;
    }

    &:hover {
      animation: zoom-in-out 1s infinite alternate;
      cursor: pointer;
      @keyframes zoom-in-out {
        from {
          transform: translateX(-0.5px) translateY(-0.5px) scale(1);
        }
        to {
          transform: translateX(-0.5px) translateY(-0.5px) scale(1.15);
        }
      }
    }

    .inner {
      width: 7px;
      height: 7px;
      
      border-radius: 50%;
    }
  }

  .knob-container {
    position: absolute;
    left: 0;
    top: -13px;
    height: 30px;
    width: calc(100% - 15px);
    display: flex;
    align-items: center;
  
    .knob {
      position: absolute;
      left: 50px;
      width: 20px;
      height: 20px;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      z-index: 2;

      .desc {
        position: absolute;
        transform: translateY(-40px);
        min-width: 100px;
        font-size: $font-size-tiny;
        font-weight: v;
        color: $color-input-form-landing-desc2;
        text-align: center;
        transition: color 0.3s ease;
        bottom: 0;
      }
      
      .desc-bottom {
        transform: translateY(-20px);
      }

      .desc-active {
        color: $color-body;
      }
    }

    .knob-default {
      background-color: $color-line;
      // border: solid 4px white;

      &:after {
        content: '';
        position: absolute;
        top: 30%;
        left: 30%;
        bottom: 30%;
        right: 30%;
        border-radius: 50%;
        background-color: white;
      }
    }

    .knob-done {
      width: 20px;
      height: 20px;
      background-color: $color-hover;
      background-image: url(../../../assets/images/common/Tick.svg);
      background-repeat: no-repeat;
      background-position: center;
      background-size: 60%;
      // border: solid 0.5px white;
    }

    
    .knob:first-child {
      justify-content: flex-start;
      .desc {
        text-align: left;
        margin-left: -5px;
      }
    }

    .knob:last-child {
      justify-content: flex-end;
      .desc {
        text-align: right;
      }
    }
  }
}
